* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  height: 100%;
  width: 100%;
  background-color: #F5F7FB;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items:center;

  background-image: url(./images/blobs.png), url(./images/blobs1.png);
  background-position: left bottom, right top;
  background-repeat: no-repeat, no-repeat;
}

 .quizTitle {
  font-family: 'Karla', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 31.25px;
  line-height: 37px;
  text-align: center;
  margin-bottom: 10px;
  color: #293264;
}
.quizDescription {
  margin-bottom:30px;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;

  color: #293264;
}

 .startButton {
  width: 100%;
  height: 60px;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #F5F7FB;

  background-color: #4D5B9E;
  border-radius: 15px;
  border: none;
  cursor: pointer;
}




.questions {
  display: flex;
  flex-direction: column;
  max-width: 800px;
}

.question-box {
  margin-bottom: 10px;
}

.question {
  font-family: 'Karla', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 19px;
  color: #293264;
}

.choices {
  display: flex;
  gap: 25px;
}

.choice {
  min-width: 100px;
  text-align: center;
  padding: 8px 8px 8px 8px;
  border: 0.771045px solid #4D5B9E;
  border-radius: 8px;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 12px;

  color: #293264;
  cursor: pointer;
}
.answer-button {
  width: 200px;
  height: 45px;
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #F5F7FB;
  margin: auto;
  margin-top: 0px;

  background: #4D5B9E;
  border-radius: 15px;
  border: none;
  cursor: pointer;
}
.selected {
  background: #D6DBF5;
}
.chosen-right {
  background: #94D7A2;
}
.chosen-wrong {
  background: #F8BCBC;
}
.ansRight {
  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  line-height: 15px;
  text-align: center;

  color: #293264;
}
.result {
  display: flex;
}